import React from 'react';
import { Link } from "react-router-dom";
import Grid from '@material-ui/core/Grid';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const RestTile = (props) => {
    return (
        <div style={{ padding: '30px' }}>
            <Grid container spacing={24} style={{ background: 'white', boxShadow: '1px 1px 0 0 #ccc', borderRadius: '12px' }}>
                <Grid item xs={9} style={{ padding: '4px' }}>
                    <div style={{ padding: '6px', paddingBottom: '3px' }}><FontAwesomeIcon style={{ color: '#558BF7' }} icon="map-marker-alt" />
                        <Link style={{ textDecoration: 'none' }} to={`/rest/${props.rest.id}`}>
                            <span style={{ fontSize: '18px', fontWeight: '500', color: 'black' }}> {props.rest.title}</span>
                        </Link>
                    </div>

                    <div style={{ padding: '6px', paddingTop: '0px', color: '#558BF7', fontWeight: '400' }}>{props.rest.cuisine}</div>
                    {
                        props.rest.references && props.rest.references.length > 0 ?
                            <div style={{ padding: '10px', display: 'flex', alignContent: 'center', justifyItems: 'evenly-spaced' }}>
                                <div style={{paddingRight: '2px'}}>
                                    <FontAwesomeIcon icon="star" />
                                </div>
                                <div>  Featured in {props.rest.references[0].site_name}
                                    {props.rest.references.length > 1 ?
                                        <span>.. +{props.rest.references.length - 1}</span>
                                        : ''
                                    }
                                </div>
                            </div> : ''
                    }
                </Grid>
                {props.rest.images && props.rest.images.length > 0 ?
                    <Grid item xs={3} style={{ padding: '0px' }}>
                        <img style={{ height: '100%', width: '100%', float: 'right', borderRadius: '0 12px 12px 0' }} src={props.rest.images[0]} />
                    </Grid>
                    : ''
                }
            </Grid>
            <Grid container spacing={24} style={{ paddingTop: '8px' }}>
                <Grid item xs={12} sm={8}  style={{ color: '#558BF7', lineHeight: '20px', fontSize: '14px', padding: '4px' }}>
                    <span>{props.rest.open_closed}</span>
                    <FontAwesomeIcon style={{ fontSize: '4px', padding: '4px 8px 4px 8px' }} icon="circle" />
                    <span>3.3 miles away</span>
                </Grid>
                <Grid item xs={12} sm={4} style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', textAlign: 'center', padding: '4px' }}>
                    <div>
                        <span style={{ fontSize: '16px', fontWeight: 'bold', lineHeight: '20px' }}><FontAwesomeIcon style={{ color: '#558BF7' }} icon="star" /> {props.rest.rating}</span>
                        <span style={{ fontSize: '12px', lineHeight: '20px' }}>/5</span>
                    </div>
                    <div>
                        <span style={{ fontSize: '16px', fontWeight: 'bold' }}><FontAwesomeIcon style={{ color: '#558BF7' }} icon="walking" /> 12</span>
                        <span style={{ fontSize: '12px' }}>min</span>
                    </div>
                </Grid>
            </Grid>
        </div>
    );

} 